import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, AppBar, Tabs, Tab, Typography, CardActionArea, CardContent, Button } from '@material-ui/core'
import SwipeableViews from 'react-swipeable-views'

const TabContainer = ({children, dir}) =>{
  return (
    <Typography component="div" dir={dir} style={{ padding: '16px 10px' }}>
      {children}
    </Typography>
  )
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
}

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
  },
  card: {
    height: 210,
    width: 170,
    marginBottom: 14
  },
  AppBar: {
    backgroundColor: '#ffffff',
    boxShadow: '0 5px 6px #f3f3f3'
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
})

class TabsType extends Component {

  state = {
    value: 0
  }

  handleChange = (event, value) => {
    this.setState({ value })
  }

  handleChangeIndex = index => {
    this.setState({ value: index })
  }

  render(){
    const { classes, theme } = this.props
    return (
      <div className={classes.root}>
      <AppBar position="static" color="default" className={classes.AppBar}>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth">
          <Tab label="大师" />
          <Tab label="灵感" />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={this.state.value}
        onChangeIndex={this.handleChangeIndex}>
        <TabContainer dir={theme.direction} >
          <div className="flex_wrap_card">
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
          </div>
          <Button variant="outlined" color="primary" className={classes.button}>
            获取更多灵感
          </Button>
        </TabContainer>
        <TabContainer dir={theme.direction} >
          <div className="flex_wrap_card">
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
            <Card className={classes.card}>
              <CardActionArea>
                <CardContent>
                  
                </CardContent>
              </CardActionArea>
            </Card>
          </div>
          <Button variant="outlined" color="primary" className={classes.button}>
            获取更多灵感
          </Button>
        </TabContainer>
      </SwipeableViews>
      
    </div>
    )
  }
}

TabsType.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
}

export default withStyles(styles, { withTheme: true })(TabsType)
