import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import { Card } from '@material-ui/core';

const styles = {
    topCard: {
        height: 190,
        marginBottom: 12
    },
    card: {
        width: 170,
        height: 210,
        marginBottom: 12
    },
    button: {
        width: '100%',
        marginTop: '16px',
        height: '42px'
    },
}

const TCard = props => {
  const { classes, notop } = props
  return (
    <div>
      {notop && 
        <Card className={classes.topCard}></Card>
      }
      <div className="flex_wrap_card">
        <Card className={classes.card}></Card>
        <Card className={classes.card}></Card>
        <Card className={classes.card}></Card>
        <Card className={classes.card}></Card>
      </div>
    </div>
  )
}


TCard.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(TCard)
