import { homeServices } from '../services'
import {observable, computed, action, runInAction} from 'mobx'


class HomeStore {
    @observable.shallow _sliderList = []

    constructor (homeServices){
        this.service = homeServices
    }
    @computed get sliderList(){
        return [...this._sliderList]
    }
    @action getSilderList = async() => {
        try {
            const data = await this.service.fetchSliderData()
            runInAction(() => this._sliderList = data.result)
        }catch(error){
            console.log(error)
        }
        
    }
}

export default new HomeStore(homeServices)