import { inspirationServices } from '../services'
import {observable, computed, action, runInAction} from 'mobx'


class InspirationStore {
    @observable.shallow _tab = []
    @observable.shallow _tabsContent = []

    constructor (inspirationServices){
        this.service = inspirationServices
    }
    @computed get tab(){
        return [...this._tab]
    }
    @computed get tabsContent(){
        return [...this._tabsContent]
    }
    @action getTabList = async() => {
        try {
            const data = await this.service.fetchTabsData()
            runInAction(() => {
                this._tab = data.tab
                this._tabsContent = data.tabsContent
            })
        }catch(error){
            console.log(error)
        }
        
    }
}

export default new InspirationStore(inspirationServices)