import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card, CardHeader, CardMedia, CardContent, CardActions, Collapse, Avatar, IconButton, Typography } from '@material-ui/core'
import FavoriteIcon from '@material-ui/icons/Favorite'
import ShareIcon from '@material-ui/icons/Share'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import classnames from 'classnames'
import { Link } from 'react-router-dom'
import top from '../../assets/images/top.png'

const styles = theme => ({
  card: {
    marginTop: 12,
    maxWidth: 400,
  },
  media: {
    paddingTop: '56.25%',
  },
  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
  flex_nowrap: {
    display: 'flex',
    overflow: 'hidden'
  },
  fav: {
    color: 'rgba(0, 0, 0, 0.54)'
  },
  favOpend: {
    color: 'red'
  }
})
const Person = props => <Link to="/person" {...props} />

class SingleCard extends Component {

  state = {
    expanded: false,
    fav: false
  }

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }))
  }
  handleFavClick = () => {
    this.setState(state => ({ fav: !state.fav }))
  }
  
  render() {
    const { classes } = this.props
    return (
      <div>
        <Card className={classes.card}>
          <CardHeader
            avatar={
              <Avatar aria-label="Recipe" className={classes.avatar}>
                <img src={top} alt=""/>
              </Avatar>
            }
            title="MarenSchabhüser"
            subheader="纸艺术品和布景设计"
            component={Person}
          />
          <CardMedia
            className={classes.media}
            image="http://pqpuavz67.bkt.clouddn.com/card1.png"
            title="Paella dish"
          />
          <CardContent>
            <Typography component="p">
            MarenSchabhüser是位于德国汉堡的创意摄影师，造纸艺术家和布景设计师。
            “她使用各种材料和技术创造出独特，创新和快乐的图像。她喜欢将图形简洁和鲜艳的色彩结合起来，并采用俏皮的工作方式。
            </Typography>
          </CardContent>
          <CardActions className={classes.actions} disableActionSpacing>
            <IconButton aria-label="Add to favorites" className={classnames(classes.fav, {
                [classes.favOpend]: this.state.fav,
              })} onClick={this.handleFavClick}>
              <FavoriteIcon />
            </IconButton>
            <IconButton aria-label="Share">
              <ShareIcon />
            </IconButton>
            <IconButton
              className={classnames(classes.expand, {
                [classes.expandOpen]: this.state.expanded,
              })}
              onClick={this.handleExpandClick}
              aria-expanded={this.state.expanded}
              aria-label="Show more">
              <ExpandMoreIcon />
            </IconButton>
          </CardActions>
          <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <Typography paragraph>
                在多特蒙德应用科学与艺术大学学习的早期，她已经发现了她对复杂手工作品的热情，并爱上了各种纸张。
              </Typography>
              <Typography paragraph>
                凭借完美主义的精神，她喜欢创造甜美的简约世界 - 以及静止镜头中的动作片段。在这里，她总是从一些快速的涂鸦开始向客户展示她的想法，然后她带来她的创意纸质作品，并在摄影的帮助下设计设计。”
              </Typography>
              
              <Typography>
                <a style={{color: '#3f51b5'}} href="https://www.marenschabhueser-studio.com/portfolio">
                查看更多关于她的作品
                </a>
              </Typography>
            </CardContent>
          </Collapse>
        </Card>
      </div>
    )
  }
}

SingleCard.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(SingleCard)
