import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Card } from '@material-ui/core'
import { Layout, TabsType, BottomNav, MidSlider, Header } from '../components'
import { observer, inject } from 'mobx-react'

const styles = {
  card: {
    height: 256,
    borderRadius: 0,
    boxShadow: '4px 4px 8px #d2d2d2'
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
}

@inject('inspirationStore')
@observer
class Inspiration extends Component {
  state = {
    name: 'inspiration',
  }

  componentDidMount(){
    this.fetchData()
  }

  fetchData = () =>{
    this.props.inspirationStore.getTabList()
  }

  render() {
    const { classes } = this.props
    return (
      <div>
        <Header>
          相遇灵感
        </Header>
        
        <Layout gap={10}>
          <Card className={classes.card}>
            <div className={classes.left}>
              <MidSlider />
            </div>
          </Card>

          <TabsType />
        </Layout>
        
        <BottomNav selected={this.state.name}/>
        <div style={{height: 66}}></div>
      </div>
    )
  }
}
Inspiration.propTypes = {classes: PropTypes.object.isRequired}

export default withStyles(styles)(Inspiration)

