import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { AppBar, Tabs, Tab, Typography, Button } from '@material-ui/core'
import SwipeableViews from 'react-swipeable-views'
import { TCard } from '../Cards'

const TabContainer = ({children, dir}) => {
  return (
    <Typography component="div" dir={dir} style={{ padding: '10px' }}>
      {children}
    </Typography>
  )
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
}

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
  },
  AppBar: {
    marginTop: '10px',
    boxShadow: '0 1px 1px #d2d2d2',
    backgroundColor: '#fff'
  },
  button: {
    width: '100%',
    marginTop: '16px',
    height: '42px'
  },
})

class TabsGroup extends Component {

  state = {
    value: 0
  }

  handleChange = (event, value) => {
    this.setState({ value })
  }

  handleChangeIndex = index => {
    this.setState({ value: index })
  }

  render(){
    const { classes, theme } = this.props
    return (
      <div className={classes.root}>
      <AppBar position="static" className={classes.AppBar}>
        <Tabs
          value={this.state.value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto">
          <Tab label="墨流" />
          <Tab label="软陶" />
          <Tab label="纸浆工艺" />
          <Tab label="写意画" />
          <Tab label="光影设计" />
          <Tab label="黑艺术" />
          <Tab label="概念设计" />
        </Tabs>
      </AppBar>
      <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.value}
          onChangeIndex={this.handleChangeIndex}
        >
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
          <TabContainer dir={theme.direction}><TCard/>
            <Button variant="outlined" color="primary" className={classes.button}>
              查看更多
            </Button>
          </TabContainer>
      </SwipeableViews>
    </div>
    )
  }
}

TabsGroup.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
}

export default withStyles(styles, { withTheme: true })(TabsGroup)
